@import url("./_fontstyle.scss");
@import url("./_nullstyle.scss");
body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #363636;
  line-height: 20px;
}

.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}

main {
  min-height: calc(100vh - 183px);
}

.active {
  background-color: #363636;
  color: #fff;
}

.slick-slider {
  max-width: 300px;
  margin: 0 auto;
}

.slide img {
  max-width: 300px;
}

.slide-hide {
  display: none;
}

@media (max-width: 420px) {
  .slick-slider {
    max-width: 260px;
  }
  .slide img {
    max-width: 260px;
  }
}/*# sourceMappingURL=App.css.map */